
"use strict";
import { ref, reactive } from "vue";
import model from "./model.js";
import Tile from "./Tile.vue";

export default {
  name: "Board",
  components: {
    Tile,
  },
  props: {
    msg: String,
    imageSrc: String,
  },
  setup() {
    const game = reactive(model.game);
    const componentKey = ref(false);
    const onClick = (i) => {
      if (model.game.slide(i)) {
        // Force re-render.
        componentKey.value = !componentKey.value;
      }
    };
    return {
      game,
      componentKey,
      onClick,
    };
  },
};
